import { ChangeEvent, FC } from "react";
import { useChangeLanguage } from "src/hooks/use-change-language";
import { useNavigate } from "react-router-dom";
import warehouses from "src/data/warehouses.json";
import { useStore } from "src/store/store";
import { useShallow } from "zustand/react/shallow";
import style from "./modal-warehouses.module.css";

export const ModalWarehouses: FC = () => {
  const { t } = useChangeLanguage();

  const navigate = useNavigate();

  const { toggleModalVisible, setHouseName, setHouseData, houseName } =
    useStore(
      useShallow(state => ({
        toggleModalVisible: state.toggleModalVisible,
        setHouseName: state.setHouseName,
        setHouseData: state.setHouseData,
        houseName: state.houseName,
      })),
    );

  const changeWarehouse = (e: ChangeEvent<HTMLInputElement>) => {
    if (e.target) {
      toggleModalVisible();
      setHouseName(e.target.name);
      setHouseData(null);
      navigate(`/${e.target.id}`);
    }
  };

  return (
    <>
      <form>
        {warehouses.map(btn => (
          <div key={btn.id} className={style.modal__radio}>
            <input
              checked={btn.name === houseName}
              id={btn.query}
              type="radio"
              name={btn.name}
              onChange={changeWarehouse}
            />
            <label htmlFor={btn.query}>{t(btn.name)}</label>
          </div>
        ))}
      </form>
    </>
  );
};
