import { FC } from "react";
import { useChangeLanguage } from "src/hooks/use-change-language";
import { ModalLanguages } from "../modal-languages/modal-languages";
import { ModalWarehouses } from "../modal-warehouses/modal-warehouses";
import { ModalNotice } from "../modal-notice/modal-notice";
import { stopPropagationHandler } from "src/utils/utils";
import { useShallow } from "zustand/react/shallow";
import { useStore } from "src/store/store";
import classNames from "classnames";
import style from "./modal.module.css";

export const Modal: FC = () => {
  const { modalType, modalVisible, toggleModalVisible } = useStore(
    useShallow(state => ({
      modalType: state.modalType,
      modalVisible: state.modalVisible,
      toggleModalVisible: state.toggleModalVisible,
    })),
  );

  const { t } = useChangeLanguage();

  return (
    <>
      <div
        className={
          modalVisible ? style.modal : classNames(style.modal, style.hide)
        }
        onClick={toggleModalVisible}
      >
        <div className={style.modal__wrapper} onClick={stopPropagationHandler}>
          <div className={style.modal__content}>
            <div className={style.modal__title}>
              {t(`modal-${modalType}-title`)}
            </div>
            {modalType === "notices" && (
              <div className={style.modal__text}>{t("modal-content")}</div>
            )}
          </div>
          <div className={style.modal__wrapper__btn}>
            {modalType === "notices" && <ModalNotice />}
            {modalType === "languages" && <ModalLanguages />}
            {modalType === "warehouses" && <ModalWarehouses />}
          </div>
        </div>
      </div>
    </>
  );
};
