import { FC } from "react";
import { useStore } from "src/store/store";
import { TG_BOT } from "src/data/urls.json";
import { getLink } from "src/utils/get-link";
import { useChangeLanguage } from "src/hooks/use-change-language";
import style from "./modal-notice.module.css";

export const ModalNotice: FC = () => {
  const toggleModalVisible = useStore(state => state.toggleModalVisible);

  const { t } = useChangeLanguage();

  const carData = useStore(state => state.carData);

  const sendBtnAnalitics = (btn: string) => {
    if (location.hostname === "tablo.wb.ru") {
      window.wba("init", { counterId: 39 });
      window.wba(btn);
    }
    toggleModalVisible();
  };

  const botBtnHandler = () => sendBtnAnalitics("Bot_T");

  const appDwnldHandler = () => sendBtnAnalitics("Install_App_T");

  return (
    <>
      <a
        onClick={botBtnHandler}
        className={style.modal__install}
        href={`${TG_BOT}?start=${carData?.carNumber}`}
        target="_blank"
      >
        {t("modal-bot")}
      </a>
      <a
        onClick={appDwnldHandler}
        className={style.modal__install}
        href={getLink()}
        target="_blank"
      >
        {t("modal-install")}
      </a>
      <button onClick={toggleModalVisible} className={style.modal__cancel}>
        {t("modal-cancel")}
      </button>
    </>
  );
};
