import { Link } from "react-router-dom";
import { FC, ReactElement } from "react";
import { useChangeLanguage } from "src/hooks/use-change-language";
import { useStore } from "src/store/store";
import { Modal } from "../modal/modal";
import { useShallow } from "zustand/react/shallow";
import { findWarehouse } from "src/utils/find-warehouse";
import style from "./layout.module.css";

interface LayoutProps {
  children?: ReactElement;
}

export const Layout: FC<LayoutProps> = ({ children }) => {
  const { t } = useChangeLanguage();

  const { carData, houseName, setModalType, toggleModalVisible } = useStore(
    useShallow(state => ({
      houseName: state.houseName,
      carData: state.carData,
      setModalType: state.setModalType,
      toggleModalVisible: state.toggleModalVisible,
    })),
  );

  const query = findWarehouse(carData);

  const openWarehousesModal = () => {
    toggleModalVisible();
    setModalType("warehouses");
  };

  const openLanguagesModal = () => {
    setModalType("languages");
    toggleModalVisible();
  };

  return (
    <>
      <header className={style.header}>
        {houseName === "/" ? (
          <Link to={`/`} className={style.header__icon}></Link>
        ) : !carData?.warehouseId && houseName ? (
          <div className={style.header__wrapper}>
            <Link to={"/"} className={style.header__icon2} />
            <div
              onClick={openWarehousesModal}
              className={style.header__container}
            >
              <div className={style.header__title}>
                <div className={style.header__queue}>
                  {t("header", { store: t(``) })}
                </div>
                <div className={style.header__house}>{t(`${houseName}`)}</div>
              </div>
              <div className={style.header__arrow} />
            </div>
            <div className={style.header__language}>
              <span onClick={openLanguagesModal}>{t("code")}</span>
            </div>
          </div>
        ) : (
          houseName && (
            <div className={style.header__wrapper}>
              <Link to={"/"} className={style.header__icon2} />
              <Link to={`/${query?.query}`} className={style.header__container}>
                <div className={style.header__title}>
                  <div className={style.header__queue}>
                    {t("header", { store: t(``) })}
                  </div>
                  <div className={style.header__house}>{t(`${houseName}`)}</div>
                </div>
              </Link>
              <div className={style.header__language}>
                <span onClick={openLanguagesModal}>{t("code")}</span>
              </div>
            </div>
          )
        )}
      </header>
      <main id={"bottom"} className={style.main}>
        {children}
      </main>
      <Modal />
    </>
  );
};
