import { useEffect } from "react";
import { useStore } from "src/store/store";

export const useAnalitics = () => {
  const translation = useStore(state => state.translation);
  const source = window.location.search
    .replace("?", "")
    .split("&")
    .map(param => param.replace(/=.+/, ""))[0];

  useEffect(() => {
    if (location.hostname === "tablo.wb.ru") {
      window.wba("init", { counterId: 39 });
      window.wba("pageview", {
        language_app: translation,
        language_browser: navigator.language,
        source: source ? source : "",
      });
    }
  }, [translation, source]);
};
