import { ChangeEvent, FC } from "react";
import { useChangeLanguage } from "src/hooks/use-change-language";
import { useStore } from "src/store/store";
import languageBtns from "src/data/languages-btns.json";
import style from "./modal-languages.module.css";

export const ModalLanguages: FC = () => {
  const { t, translation, changeLanguage } = useChangeLanguage();

  const toggleModalVisible = useStore(state => state.toggleModalVisible);

  const changeLanguageHandler = (e: ChangeEvent<HTMLInputElement>) => {
    toggleModalVisible();
    changeLanguage(e);
  };

  return (
    <>
      <form>
        {languageBtns.map(btn => (
          <div key={btn.id} className={style.modal__radio}>
            <input
              checked={btn.id === translation}
              id={btn.id}
              type="radio"
              name={btn.text}
              onChange={changeLanguageHandler}
            />
            <label htmlFor={btn.id}>{t(btn.text)}</label>
          </div>
        ))}
      </form>
    </>
  );
};
