import { ICar } from "src/api/car";
import warehouses from "src/data/warehouses.json";

export const findWarehouse = (data: ICar | ICar[] | null) => {
  if (Array.isArray(data) || data === null) {
    return warehouses.find(warehouse => {
      return location.href
        .split("/")
        .find(query => query.split("?").includes(warehouse.query));
    });
  } else {
    return warehouses.find(warehouse => warehouse.id === data?.warehouseId);
  }
};
