import { SyntheticEvent } from "react";
import { ICar } from "src/api/car";

export const editCarNumber = (number: string) => {
  return number.replace(/\d+/g, str => ` ${str} `);
};

export const splitCarNumber = (carNumber: string, splitValue: string) => {
  return editCarNumber(carNumber).split(splitValue);
};

export const filtereData = (houseData: ICar[] | null, searchValue: string) => {
  if (houseData) {
    return houseData.filter(item => {
      return item.carNumber.replace(/\D/g, "").startsWith(searchValue);
    });
  }
};

export const stopPropagationHandler = (e: SyntheticEvent) => {
  e.stopPropagation();
};

export const preventDefaultHandler = (e: SyntheticEvent) => {
  e.preventDefault();
};

export const onMouseDownLinkHandler = (
  e: SyntheticEvent<HTMLAnchorElement>,
) => {
  e.currentTarget.click();
};
