import { useTranslation } from "react-i18next";
import { useShallow } from "zustand/react/shallow";
import { useStore } from "src/store/store";
import { ChangeEvent } from "react";
import "src/i18next";

export const useChangeLanguage = () => {
  const { t, i18n } = useTranslation();

  const { translation, setTranslation } = useStore(
    useShallow(state => ({
      setTranslation: state.setTranslation,
      translation: state.translation,
    })),
  );

  const changeLanguage = (e: ChangeEvent<HTMLInputElement>) => {
    if (e.target) {
      const input = e.target;
      i18n.changeLanguage(input.id);
      setTranslation(input.id);
    }
  };

  return { t, translation, changeLanguage };
};
